.container {
  min-width: 320px; /* Adjusted min-width for smaller screens */
  max-width: 750px;
  display: flex;
  flex-direction: column;
  gap: 15px; /* Reduced gap for smaller screens */
  box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2);
  padding-top: 5px;
  height: fit-content;
  margin: 0 auto;
}

.header {
  display: flex;
  gap: 4px;
  overflow-x: auto;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  
}

.header::-webkit-scrollbar {
  height: 7px;
}

.header::-webkit-scrollbar-thumb {
  border-radius: 20px;
  background-color: #c7c7c7;
}

.header .section {
  padding: 10px;
  border-bottom: 2px solid transparent;
  font-weight: 500;
  font-size: 1rem;
  white-space: nowrap;
  cursor: pointer;
}

.header .active {
  border-bottom: 2px solid #239ce2;
  color: #239ce2;
}

.body {
  padding: 30px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding-top: 0;
}

.detail {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.detail .row {
  display: flex;
  gap: 20px;
}
.detail .column {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.detail .row > div {
  flex: 1;
}

.chips {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 20px;
}

.chips .new {
  color: #239ce2;
  font-weight: bold;
  letter-spacing: 1px;
  cursor: pointer;
}

.chip {
  display: flex;
  gap: 5px;
  align-items: center;
  padding: 4px 8px;
  background-color: #808080;
  border-radius: 20px;
  cursor: default;
}

.chip p {
  font-weight: 500;
  line-height: 1.25rem;
  color: #fff;
}
.chip svg {
  color: #fff;
  height: 18px;
  width: 18px;
  cursor: pointer;
}

.chips .active {
  background-color: #239ce2;
}

.body button {
  width: fit-content;
  padding: 8px 16px;
  border-radius: 5px;
  background-color: #239ce2;
  color: #fff;
  outline: none;
  border: none;
  font-weight: 500;
  font-size: 1rem;
  letter-spacing: 1px;
  display: flex;
  gap: 5px;
  align-items: center;
  cursor: pointer;
  transition: 200ms;
}

.body button:active {
  transform: translateY(2px);
}

@media (max-width: 768px) {
  .container {
    min-width: 280px; /* Adjusted min-width for smaller screens */
    gap: 10px; /* Reduced gap for smaller screens */
  }

  .header {
    gap: 2px; /* Reduced gap for smaller screens */
  }

  .header::-webkit-scrollbar {
    height: 5px; /* Reduced scrollbar height */
  }

  .detail .row {
    flex-direction: column; /* Stacking rows on smaller screens */
    gap: 10px; /* Reduced gap for smaller screens */
  }

  .detail .column {
    gap: 5px; /* Reduced gap for smaller screens */
  }
  
  .chips {
    gap: 10px; /* Reduced gap for smaller screens */
  }
  
  .chip p {
    font-size: 0.9rem; /* Adjusted font size for smaller screens */
  }
  
  .body button {
    font-size: 0.9rem; /* Adjusted font size for smaller screens */
  }
}